body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}



.login {
    background-attachment: fixed;
    background: burlywood;
    background-size: cover;
    width: 100%;
    height: 100%;
}
.Button{
    color:  blue;
    text-align: center;
    padding: 0;
    margin: 0;
    height: 30px;
    width: 100px;
    background: beige;
}
.Button:hover {
        background: skyblue;
}
.form {
    margin-top: 40px;
}
.form input {
    position: relative;
    margin-top: 0.9rem;
    box-shadow: 0 1px 5px 2px;

}
.form button {
    margin-top: 0.3rem;
}
.daftar {
    position: relative;
    margin-left: 60%;
}
.Tollbar{
    background: rgb(30%, 30%, 40%);
    height: 100px;
    width: 100%;
}
.logo{
    text-align: center;
    width: 50px;
    height: 60px;
    float: left;
    margin-left: 25px;
    margin-top: 10px;
    border-radius: 50%;
}
.loginlogout {
    float: right;
    margin-top: 10px;
    margin-right: 5px;

}

.loginlogout li {
    list-style: none;
    float: left;
    margin: 5px;
}
.content {
    text-align: center;
    float: left;
    margin-left: 35px;
    box-shadow: 0 0 3px 2px; 
    width: 300px;
    height: 400px;
    margin-top: 20px;
    margin-bottom: 3px;
  }
  .ImageContent{
    width: 200px;
    height: 150px;
    border-radius: 50%;
  }
